.ma-header {
  background: #fcdb00;
  padding-top: 47px;
}

.navbar {
  border: none;
  background: none;
}

.navbar__link {
  display: block;
  color: #010101;
  font-family: "PTSansBold", sans-serif;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

.navbar__link:hover {
  border-bottom: 1px solid #010101;
}

.navbar__link--font {
  font-family: "HelveticaBold", sans-serif;
}

.navbar__item {
  margin-top: 13px;
}

.navbar__item:nth-child(2) {
  margin-left: 43px;
  margin-right: 42px;
}

.navbar-brand__logo {
  float: left;
  border-radius: 50%;
}

.navbar-brand__logo:hover {
  background: #5c5c5c;
}

.navbar-brand__title {
  float: left;
  margin-top: 1px;
  color: #010101;
  font-family: "PTSansBold", sans-serif;
  font-size: 30px;
  font-weight: 700;
  margin-left: 11px;
}

.navbar-default .navbar-nav > li > a {
  color: #010101;
}

.ma-slider {
  padding: 24px 0 98px;
  background: #fcdb00;
}

.ma-carousel {
  padding: 10px 0 10px;
}

.ma-carousel-text {
  margin-top: 48px;
}

.ma-carousel-text__title {
  color: #010101;
  font-family: "PTSansBold", sans-serif;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 22px;
}

.ma-carousel-text__subtitle {
  width: 70%;
  color: #313841;
  font-family: "PTSansRegular", sans-serif;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 18px;
  line-height: 22px;
}

.ma-carousel-text__description {
  width: 87%;
  color: #555555;
  font-family: "PTSansRegular", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 40px;
  line-height: 20px;
}

.ma-carousel-text__icon {
  color: #010101;
  font-size: 42px;
  font-weight: 400;
}

.ma-carousel-text__icon:hover {
  transform: scaleY(1.1);
}

.ma-carousel-text__icon--center {
  margin: 0 33px;
}

.ma-carousel-ipad__base {
  padding: 44px 14px 43px;
  background: #ffffff;
  border-radius: 24px;
  width: 85%;
  margin-left: 11px;
}

.ma-carousel-ipad__base:hover {
  background: #2a0438;
}

.ma-carousel-ipad__screen {
  padding: 100px 0 100px;
  background: #000000;
  display: block;
  text-align: center;
}

.ma-carousel-ipad__screen:hover {
  background: #032a04;
}

.ma-carousel-ipad__title {
  display: inline-block;
  color: #fcdb00;
  font-family: "PTSansBold", sans-serif;
  font-size: 30px;
  font-weight: 700;
  margin: 55px 0 94px;
}

.ma-carousel-ipad__img {
  border-radius: 50%;
}

.ma-carousel-ipad__img:hover {
  background: gray;
}

.ma-carousel-indicators {
  bottom: -62px;
}

.carousel-indicators li {
  width: 13px;
  height: 13px;
  background-color: rgba(255, 255, 255, 0.5);
}

.carousel-indicators .active {
  width: 13px;
  height: 13px;
  margin: 0;
}

.ma-features {
  padding: 93px 0 81px;
  margin-left: -12px;
}

.ma-features__title {
  color: #010101;
  font-family: "PTSansBold", sans-serif;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 16px;
}

.ma-features__description {
  color: #313841;
  font-family: "PTSansRegular", sans-serif;
  font-size: 20px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 88px;
  line-height: 21px;
}

.ma-features__row {
  margin-bottom: 72px;
}

.ma-features__icon {
  color: #010101;
  font-size: 53px;
  font-weight: 400;
}

.ma-features__subtitle {
  color: #010101;
  font-family: "PTSansRegular", sans-serif;
  font-size: 22px;
  font-weight: 400;
  margin-top: 2px;
}

.ma-features__subtitle--large {
  text-transform: uppercase;
}

.ma-features__subtitle-text {
  color: #898989;
  font-family: "PTSansRegular", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.ma-subscribe {
  padding: 73px 0 96px;
  background: #010101;
}

.ma-subscribe:hover {
  background: #17191b;
}

.ma-subscribe__title {
  color: #fcdb00;
  font-family: "PTSansBold", sans-serif;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 10px;
  text-align: center;
}

.ma-subscribe__description {
  color: #898989;
  font-family: "PTSansRegular", sans-serif;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 34px;
  text-align: center;
}

.ma-form {
  margin-bottom: 72px;
}

.ma-form__input {
  width: 105%;
  padding: 18px 0 16px 24px;
  background: #ffffff;
  border: none;
  border-radius: 5px;
}

.ma-form__btn {
  margin-left: 33px;
  padding: 13px 34px 14px 37px;
  border-radius: 5px;
  border: none;
  background-color: #fcdb00;
  color: #010101;
  font-family: "PTSansBold", sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.ma-form__btn:hover {
  box-shadow: 0px 0px 2px 2px white;
}

input::-webkit-input-placeholder {
  color: #898989;
  font-family: "PTSansRegular", sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.ma-social-icon {
  color: #fcdb00;
  font-size: 42px;
  font-weight: 400;
  margin: 0 14px;
}

.ma-social-icon:hover {
  transform: scale(1.1);
}

.ma-social-icon:first-child {
  margin-left: 0;
}

.ma-social-icon:last-child {
  margin-right: 0;
}

.ma-footer {
  padding: 30px 0 117px;
  background: #010101;
  color: #ffffff;
  font-family: "PTSansRegular", sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.ma-footer:hover {
  background: #17191b;
}

.ma-footer__links {
  list-style-type: none;
  margin-left: -10px;
}

.ma-footer__link {
  text-decoration: none;
  color: #ffffff;
}

.ma-footer__link:hover {
  text-decoration: none;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
}

.ma-footer__address {
  margin-bottom: 0;
}

@font-face {
  font-family: "PTSansRegular";
  src: url("../fonts/PTSans/PTSansRegular/PTSansRegular.eot");
  src: url("../fonts/PTSans/PTSansRegular/PTSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/PTSans/PTSansRegular/PTSansRegular.woff") format("woff"), url("../fonts/PTSans/PTSansRegular/PTSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "PTSansBold";
  src: url("../fonts/PTSans/PTSansBold/PTSansBold.eot");
  src: url("../fonts/PTSans/PTSansBold/PTSansBold.eot?#iefix") format("embedded-opentype"), url("../fonts/PTSans/PTSansBold/PTSansBold.woff") format("woff"), url("../fonts/PTSans/PTSansBold/PTSansBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "HelveticaBold";
  src: url("../fonts/Helvetica/HelveticaBold/HelveticaBold.eot");
  src: url("../fonts/Helvetica/HelveticaBold/HelveticaBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Helvetica/HelveticaBold/HelveticaBold.woff") format("woff"), url("../fonts/Helvetica/HelveticaBold/HelveticaBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  transition: all 0.3s ease-in;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body, html {
  min-height: 100%;
}

@media (min-width: 1200px) {
  .container {
    width: 967px;
  }
}

@media (max-width: 767px) {
  .navbar-collapse {
    border: none;
    text-align: center;
  }
  .navbar__item:nth-child(2) {
    margin-left: 0px;
    margin-right: 0px;
  }
  .ma-features__icon {
    display: none;
  }
  .ma-features__subtitle {
    text-align: center;
  }
  .ma-features__subtitle-text {
    text-align: center;
  }
  .ma-features__row {
    margin-bottom: 10px;
  }
  .ma-social-icons {
    text-align: center;
  }
  .ma-footer__links-item {
    display: inline-block;
    margin: auto 10px;
  }
  .ma-footer-address-info {
    text-align: center;
  }
}

@media (max-width: 991px) {
  .ma-carousel-ipad {
    display: none;
  }
  .ma-carousel-text {
    text-align: center;
  }
  .ma-carousel-text__subtitle, .ma-carousel-text__description {
    width: 100%;
  }
}

@media (max-width: 696px) {
  .ma-form__label {
    width: 95%;
    margin-bottom: 15px;
  }
  .ma-form__btn {
    margin-left: 14px;
    padding: auto 0;
    width: 94%;
    text-align: center;
  }
}

@media (max-width: 410px) {
  .ma-form__btn {
    width: 92%;
  }
}

@media (max-width: 605px) {
  .ma-footer-references {
    width: 100%;
  }
  .ma-footer__links {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
}
